import {HttpClientModule} from "@angular/common/http";
import {MultiPickerModule} from "ion-multi-picker";

@NgModule({
  declarations: [
    WorkInfoPage,
  ],
  imports: [
    IonicPageModule.forChild(WorkInfoPage),
    MultiPickerModule,
    HttpClientModule
  ],
  providers:[
    IdentityInfoProvider
  ]
})
export class WorkInfoPageModule {}



